<template>
    <div class="login">
        <div class="row">
            <div class="col s12 m10 offset-m1 l8 offset-l2 xl6 offset-xl3">
                <div class="card-panel hoverable">
                    <div class="switch right">
                        <label>
                        Acceso
                        <input type="checkbox" v-model="pin">
                        <span class="lever green"></span>
                        Registro
                        </label>
                    </div>
                    <h3 class="justificado">{{pin ? 'Registro' : 'Acceso'}}</h3>
                    <form>
                        <div>
                            <div class="row" v-if="pin">
                                <div class="input-field col s6">
                                    <input id="first_name" type="text" class="validate">
                                    <label for="first_name">First Name</label>
                                </div>
                                <div class="input-field col s6">
                                    <input id="last_name" type="text" class="validate">
                                    <label for="last_name">Last Name</label>
                                </div>
                                <div class="input-field col s6">
                                    <input id="dni" type="text" class="validate">
                                    <label for="dni">DNI</label>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="input-field col s12">
                                <input id="email" type="email" class="validate">
                                <label for="email">Email</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="input-field col s12">
                            <input id="password" type="password" class="validate">
                            <label for="password">Password</label>
                            </div>
                        </div>
                        <div class="row" v-if="pin">
                            <div class="input-field col s12">
                            <input id="password2" type="password" class="validate">
                            <label for="password2">Repita el Password</label>
                            </div>
                        </div>
                        <div class="row">
                            <div class="input-field col s12">
                            <input type="submit" :value="pin ? 'Registrar' : 'Acceder'" class="btn waves-effect green">
                            </div>
                        </div>
                    </form>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
export default {
    name : "login",
    data : function(){
        return {
            pin : false
        }
    }
}
</script>
